#async_typeahead > li > a {
    white-space: initial;
}

.divider {
    height: 1px;
    width: 100%;
    display: block;
    margin: 9px 0;
    overflow: hidden;
    background-color: #e5e5e5;
}


.play-cursor-default > div > img {
    cursor: default !important
}

.play-cursor-default > div > div {
    cursor: default !important
}

.play-container {
    position: relative;
}

.title {
    line-height: 1.05;
    font-weight: 600 !important;
    transition: all .2s ease-in-out;
}

.author-nick {
    color: #606060;
    font-size: small;
}

.m-button {
    margin-right: 0;
    display: flex;
    margin-left: auto;
}

.m-button:focus {
    outline: none !important;
    outline-offset: none !important;
}

.react-ripples {
    border-radius: 20px !important;
}

.toast-root {
    bottom: 8px;
    left: 8px;
    z-index: 1400;
    position: fixed;
    align-items: center;
    justify-content: center;
}

.dropdown-toggle:after {
    display: none !important
}

.dropdown-menu.show {
    opacity: 1 !important;
    pointer-events: auto !important;
}

.dropdown-menu {
    box-shadow: 0 10px 10px #666;
}

.rbt-input {
    width: 100% !important;
}

.film-views {
    line-height: 0.6;
    color: #606060;
}

.cursor-pointer {
    cursor: pointer !important
}

.cursor-default {
    cursor: default !important
}

.suspense-loader {
    display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    left: 0;
}

.fetch-loader {
    width: 100%;
    height: 32px;
}

.button-ripple {
    box-shadow: 0 0 0 !important;
    position: relative;
    overflow: hidden;
}

.button-ripple-24 {
    border: none;
    border-radius: 20px;
    width: 24px;
    height: 24px;
    color: black
}

.button-ripple-div-next-width {
    flex: 0 0 auto !important;
    width: calc(100% - 26px) !important;        
}

.form-select.no-validate:valid {
    border-color: #ced4da;
    padding-right: .75rem;
    background: none;
}

@media(hover: hover) and (pointer: fine)  {

    .play-outer-container {
        cursor: pointer;
    }

    .play-image {
        cursor: pointer;
        opacity: 1;
        display: block;
        width: 100%;
        height: auto;
        transition: .5s ease;
        backface-visibility: hidden;
    }

    .play-middle {
        cursor: pointer;
        transition: .5s ease;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
        text-align: center;
    }

    .play-outer-container:hover .play-image {
        opacity: 0.5;
    }
    
    .play-outer-container:hover .play-middle {
        opacity: 1.0;
    }
    
    .play-outer-container:hover .playlist-add-icon {
        opacity: 0.6;
    }
    
    .play-outer-container:hover .playlist-add-icon-holder:hover .playlist-add-icon {
        opacity: 1;
    }

    .remove-holder {
        opacity: 0;
        transition: all .2s ease-in-out;
        color: #606060;
    }
    .remove-container:hover .remove-holder {
        opacity: 0.6;
    }
    .remove-container:hover .remove-holder:hover {
        opacity: 1;
        cursor: pointer;
    }

    .button-ripple-24:hover, .button-ripple-24:active {
        background-color: hsla(0, 0%, 0%, 0.15);
        color: black
    }


@keyframes useRippleAnimation {
    to {
        transform: scale(15);
        opacity: 0;
    }
}


}

@media (pointer: coarse) {

    .remove-holder {
        opacity: 0.8;
        color: #606060;
    }

    .play-image {
        cursor: pointer;
        opacity: 1;
        display: block;
        width: 100%;
        height: auto;
        backface-visibility: hidden;
    }

    .play-middle {
        cursor: pointer;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
        text-align: center;
    }


    @keyframes useRippleAnimation { 
        from {
            transform: scale(0);
            opacity: 1;
        }
        to {
            transform: scale(0);
            opacity: 1;
        }
    }


}

@media(max-width:586px) {
    .search-bar {
        width: 100%;
    }
}
